import axios from "axios";
import { api } from "@/services/config.js";
console.log(api);
export function equipmenttype_equipments(queryString) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "GET",
    url: `${api}/equipmenttype/GetEquipType_List?${queryString}` + queryString,
    mode: "no-cors",
  });
}
export function create_equipmenttype_equipment(body) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "POST",
    url: "${api}/equipmenttype_detail/Save_EquipmentType",
    data: body,
    mode: "no-cors",
  });
}
export function update_equipmenttype_equipment(body) {
  body.saveType = "update";
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "POST",
    url: "${api}/equipmenttype_detail/Save_EquipmentType",
    data: body,
    mode: "no-cors",
  });
}
export function delete_equipmenttype_equipment(body) {
  return axios({
    headers: {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
      "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE",
    },

    method: "DELETE",
    url: "${api}/equipmenttype/Delete_EquipmentType",
    data: { idxs: [`${body.equipment_type_idx}`] },
    mode: "no-cors",
  });
}
